
.headerDiv {
    background-color: #D9D9D9;
    height: 100%;
    position: sticky;
    top: 0px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1); /* 수정 가능 */
}

.blank {
    padding-top: 4rem;
}

.contentContainer {
    color: white;
}

.titleContainer {
    margin-left: 1rem;
    font-size: 3rem;
    font-weight: 800;
    text-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
}

.tabContainer {
    margin-top: -1rem;
    margin-left: 1rem;
}

.dividerHr {
    border: 1px solid white;
}

.headerButton {
    font-size: 1.5rem; 
    background-color: transparent; 
    color: white; 
    border: none; 
    padding: 1rem 20px; 
    cursor: pointer;
    transition: box-shadow 0.2s ease; 
    
    &:hover {
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
    }
}

.headerButton.selected {
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}