@import url("https://fonts.googleapis.com/css2?family=Song+Myung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Gowun+Dodum&family=Nanum+Gothic:wght@400;700;800&display=swap');

.container {
  display: flex;
  flex-direction: row;
  /* width: 1800px; */
  height: 1000px;
}

.sidebarContainer {
  position: relative;
  font-family: 'Gowun Dodum', sans-serif;
  min-height: 55rem;
  color: white;
  transition: transform 0.3s width 0.3s;
}

.sidebarContainer.visible {
  transform: translateX(0); /* 사이드바를 보이게 함 */ 
  visibility: visible;
  width: 21rem;
}

.sidebarContainer.hidden {
  transform: translateX(-100%); /* 사이드바를 왼쪽으로 숨김 */
  visibility: hidden;
  width: 0rem;
}

.sidebarButton {
  border-radius: 20px;
  border: none; 
  background-color: #939393;
  color: white;
  font-size: 2rem;
  width: 3rem;
  height: 3rem;
  z-index: 1;
  position: absolute;
  left: 20rem;
  transition: transform 0.3s;
}

.sidebarButton.fold {
  transform: translateX(0rem); 
}

.sidebarButton.open {
  transform: translateX(-21rem);
}


.mainContainer{
  width: 100%;
  min-height: 55rem;
  font-family: 'Gowun Dodum', sans-serif;
  background-color: whitesmoke;
  font-size: 30px;
  overflow: scroll;
  /* position: relative; */
  transition: transform 0.3s, width 0.3s;
}

.mainContainer.normal {
  /* transform: translateX(0rem); 사이드바를 보이게 함 */
  width: 100%;
}

.mainContainer.extended {
  /* transform: translateX(-21rem); 사이드바를 왼쪽으로 숨김 */
  width: 100%;
}

