.sidebar {
    top: 0px;
    position: sticky;
    background-color: #939393;
    text-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
    box-shadow: 5px 0px 20px rgba(0, 0, 0, 0.2);
    z-index: 10;
    width: 21rem;
    height: 100%;
}

#topProfile {
    text-align: center;
}

#profileImage {
    width: auto;
    height: 100px;
    margin-top: 24%;
    margin-left: 3.6rem;
    z-index: 2;
    position: absolute;
}

#profileBackground {
    width: auto;
    height: 200px;
    margin-top: 10%;
    z-index: 1;
}

.textTag {
    margin-top: 3rem;
    margin-left: 1rem;
    font-size: 30px;
    font-weight: 800;
}

.normalText {
    font-size: 20px;
    font-weight: 400;
}

nav {
    font-family: 'Gowun Dodum', sans-serif;
    padding-left: 27px;
    margin-top: 30px;
    margin-bottom: 30px;
    font-size: 20px;
}

.side-link, .side-link:visited {
    padding-left: 27px;
    text-decoration: none;
    color: white;
    display: block;
    font-size: 1.5em;
    transition: margin-left 0.3s;
  }
  
  .side-link:hover, .side-link:active {
    color: white;
    background-color: rgba(50, 69, 0, 0.2);
    margin-left: 30px;
  }  